.stars {
  display: flex;

  &-star{
    width: 16px;
    height: 16px;
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 4px;

  &:not(:last-child) {
    background-image: url('../images/star-active.svg')
  }

  &:last-child {
    background-image: url('../images/star.svg')
  }
  }
}
